.collapsible-container {
  height: 140px;
  max-height: 40px;
  overflow: hidden;
  position: relative;
  margin-bottom: 20px;
  transition: max-height 0.5s ease; }
  .collapsible-container > .heading {
    padding: 10px 0 10px 30px;
    font-size: 20px;
    text-transform: capitalize; }
    @media (max-width: 800px) {
      .collapsible-container > .heading {
        font-size: 16px; } }
  .collapsible-container > .content {
    line-height: 20px;
    padding: 0 30px 0 30px;
    margin-bottom: 10px;
    font-size: 16px; }
    @media (max-width: 800px) {
      .collapsible-container > .content {
        font-size: 14px; } }
  .collapsible-container > .toggle-btn {
    position: absolute;
    left: 0;
    top: 10px;
    width: 20px;
    height: 20px;
    transform: rotate(-90deg);
    cursor: pointer;
    transition: transform 0.5s ease; }
  .collapsible-container.expanded {
    max-height: 140px; }
    .collapsible-container.expanded > .toggle-btn {
      transform: rotate(0deg); }

.animate-heading {
  overflow: hidden;
  border-right: solid 1.5px #3131f3;
  white-space: nowrap;
  animation: typing 3s steps(20, end) infinite, blink-caret 0.75s step-end infinite; }

/* The typing effect */
@keyframes typing {
  from {
    width: 0; }
  to {
    width: 100%; } }

/* The typewriter cursor effect */
@keyframes blink-caret {
  from,
  to {
    border-color: transparent; }
  50% {
    border-color: #3131f3; } }

header {
  position: fixed;
  width: 100%;
  padding: 20px 0;
  z-index: 1;
  background-color: #ffffff; }
  header.sticky {
    box-shadow: 0px 0px 5px 1px #000000; }
  header nav .name-logo {
    font-size: 30px;
    float: left;
    font-weight: 700; }
    header nav .name-logo span {
      margin-right: 5px;
      cursor: pointer; }
  header nav .menu-btn {
    float: right;
    padding-top: 8px;
    display: none; }
    @media (max-width: 800px) {
      header nav .menu-btn {
        display: inline-block; } }

.header-menu {
  float: right;
  padding: 10px 0; }
  .header-menu .header-links {
    display: inline-block;
    margin-right: 20px;
    font-size: 16px;
    color: #000000;
    text-transform: uppercase;
    vertical-align: middle; }
    @media (max-width: 800px) {
      .header-menu .header-links {
        display: block;
        padding: 20px 0;
        color: #ffffff; } }
    .header-menu .header-links a {
      color: inherit;
      position: relative; }
      .header-menu .header-links a::before, .header-menu .header-links a::after {
        content: "";
        display: block;
        position: absolute;
        bottom: -10px;
        width: 100%;
        height: 2px;
        background-color: currentColor;
        transition-property: transform;
        transition-timing-function: cubic-bezier(0.5, 0, 0.1, 1); }
      .header-menu .header-links a::before {
        left: 0;
        transform: scaleX(0);
        transform-origin: left center;
        transition-duration: 0s; }
      .header-menu .header-links a::after {
        right: 0;
        transform: scaleX(0);
        transform-origin: right center;
        transition-duration: 0.4s; }
      .header-menu .header-links a:hover::before {
        transform: scale(1);
        transition-duration: 0.4s; }
      .header-menu .header-links a:hover::after {
        transform: scaleX(1);
        transition-duration: 0s;
        transition-delay: 0.4s; }
  @media (max-width: 800px) {
    .header-menu {
      position: absolute;
      top: 70px;
      left: 0;
      width: 100vw;
      height: 0;
      padding: 0;
      background-color: #000000ee;
      text-align: center;
      overflow: hidden;
      transition: height 0.5s ease, padding 0.5s ease; }
      .header-menu.show {
        height: 100vh;
        padding: 50px 0; } }

